body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  box-sizing: border-box;
  cursor: default;
  color: #333; }

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  font-size: .875em;
  font-family: 'Source Code Pro', monospace, sans-serif;
  font-weight: 400;
  box-sizing: inherit; }

#root {
  width: 100vw;
  height: 100vh; }

.mainApp {
  height: 100%;
  width: 100%;
  padding: .5rem;
  display: grid;
  grid-template-rows: 1.5fr 3rem 7fr;
  grid-auto-columns: 1fr; }

.editor__text_areas {
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: stretch;
  height: 100%;
  min-height: 100%;
  width: 100%; }
  .editor__text_areas--input {
    flex: 1 1;
    white-space: pre;
    padding: 1rem; }
  .editor__text_areas--output {
    flex: 1 1;
    white-space: pre;
    padding: 1rem; }

.header {
  padding: 1rem;
  display: flex;
  justify-content: space-between; }
  .header h1 {
    margin-bottom: 1rem;
    flex: 1 1; }
  .header div {
    flex: 2 1;
    display: flex; }

.menu {
  display: flex;
  flex-direction: row; }
  .menu input {
    width: 20rem;
    padding: 12px 20px;
    margin: .2rem 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box; }
  .menu__btn_group {
    margin-left: auto; }
    .menu__btn_group__container {
      display: flex; }
      .menu__btn_group__container__button {
        box-shadow: inset 0px 1px 3px 0px #91b8b3;
        background-color: #768d87;
        border-radius: 3px;
        border: 1px solid #566963;
        display: inline-block;
        cursor: pointer;
        color: #ffffff;
        padding: .4rem .8rem;
        text-decoration: none;
        text-shadow: 0px -1px 0px #2b665e;
        margin-right: 1rem; }
        .menu__btn_group__container__button:hover {
          background-color: #6c7c7c; }
        .menu__btn_group__container__button:active {
          position: relative;
          top: 1px; }
      .menu__btn_group__container a > img {
        width: 2rem; }
  .menu__checkbox_group {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    margin-left: 1rem;
    flex: 1 1 auto; }
    .menu__checkbox_group > :not(label) {
      margin-left: 0.5rem; }
    .menu__checkbox_group input {
      width: 1.5rem;
      height: 3rem; }
    .menu__checkbox_group label {
      line-height: 3rem; }

